import { AfterViewInit, Component, Input, OnChanges } from '@angular/core';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';
import { BcProgressBarBehavior } from '@bancolombia/design-system-behaviors';
/**
 * Componente barra de progreso.
 * La barra de progreso indica el estado actual
 * de lo que se está procesando.
 * @export
 * @class BcProgressBarComponent
 */
@Component({
  selector: 'bc-progress-bar',
  templateUrl: `./bc-progress-bar.component.html`,
})
export class BcProgressBarComponent implements OnChanges, AfterViewInit {
  /**
   *
   *
   * @type {number}
   * @memberof BcProgressBarComponent
   */
  @Input() value: number;

  /**
   *
   *
   * @type {('lineal' | 'circle' | 'semi-circle')}
   * @memberof BcProgressBarComponent
   */
  @Input() type: 'lineal' | 'circle' | 'semi-circle' = 'lineal';

  /**
   *
   *
   * @type {string}
   * @memberof BcProgressBarComponent
   */
  @Input() color: string;

  /**
   *
   *
   * @type {number}
   * @memberof BcProgressBarComponent
   */
  @Input() diameter: number;

  /**
   * Id del elemento planner.
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */
  @Input() componentId = this.bcHelperService.getId(this);

  /**
   * Tipo de color del background.
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */
  @Input() backgroundColor: 'light' | 'dark' = 'light';

  /**
   * Grosor del circulo
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */
  @Input() stroke: string;


  /**
   * Orientacion vertical o horizontal
   *
   * @type {string}
   * @memberOf BcPlannerComponent
   */
   @Input() orientation: string;

  constructor(private bcHelperService: BcHelperService) {}

  /**
   *
   *
   * @type {BcProgressBarBehavior}
   * @memberof BcProgressBarComponent
   */
  behavior: BcProgressBarBehavior;

  firstTime: boolean = false;

  ngOnChanges(changes) {
    if (this.firstTime) {
      if (changes.value) {
        this.behavior.setProgress(this.value);
      }
    }
    this.firstTime = true;
  }

  ngAfterViewInit(): void {
    this.behavior = new BcProgressBarBehavior();
    this.behavior.setId(this.componentId);
    this.behavior.setUp();
  }
}
